.bm-item {
    display: inline-block;
    text-decoration: none;
    margin-bottom: 10px;
    color: #d1d1d1;
    transition: color 0.2s;
  }
  
  .bm-item:hover {
    color: white;
  }
  
  .bm-burger-button {
    position: fixed;
    width: 30px;
    height: 25px;
    left: 36px;
    top: 36px;
  }
  
  .bm-burger-bars {
    background: #373a47;
  }
  
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  .bm-cross {
    background: #bdc3c7;
  }
  
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  .bm-morph-shape {
    fill: #373a47;
  }
  
  .bm-item-list {
    color: #b8b7ad;
  }
  
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

  .menu {
      width: 225px !important;
  }

  .menuItem {
    display: flex;
    flex-direction: row;
    text-decoration: normal;
    color: white;
    text-decoration: none;
  }

  .menuItem:hover {
      color: lightblue
  }

  .menuText {
      text-decoration: none;
      font-size: 20px;
      font-weight: 100;
      margin-left: 25px;
      margin-top: -2%;
      font-family: Futura,Trebuchet MS,Arial,sans-serif;
  }