.firstItem {
    border-bottom: 1px solid rgba(255,255,255,0.15);
    border-top: 1px solid rgba(255,255,255,0.15);
    color: white;
    margin: 0;
    padding-bottom: 20px;
    padding-top: 20px;
    text-align: left;
    text-decoration: none;
    transition: background-color 1s ease-out;
    width: 100%
}

.item {
    border-bottom: 1px solid rgba(255,255,255,0.15);
    color: white;
    padding-bottom: 20px;
    margin: 0;
    padding-top: 20px;
    text-align: left;
    text-decoration: none;
    width: 100%;
    transition: background-color 1s ease-out;
}

